import Swal from 'sweetalert2';
import { jpAxios } from "./JSP";
const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
        confirmButton: 'btn btn-success p-2 mx-2',
        cancelButton: 'btn btn-danger p-2 mx-2',
    },
    buttonsStyling: false,
})


// Post User 
export  const PostUsersServise = async (data , Navigate) => {
  
    try {
        const Res = await jpAxios.post("/users", data);
        if (Res.status === 201) {
            swalWithBootstrapButtons.fire(
                '  افزوده شد ',
                '  ! کاربر مورد نظر  ',
                'success'
            )
            Navigate(-1)
        } else {
            throw (Res)
        }
    }catch (err) {
            swalWithBootstrapButtons.fire(
                '  مشکلی پیش آمده ',
                ' ! اینترنت شما قطع است',
                'question'
            )
            Navigate(-1)


    }  
} 

// Put or Edite User 
export const UsersPutServise = async (data,id , Navigate) => {
  
    try{
        const res = await  jpAxios.put(`/users/${id}`, data);
        if(res.status === 200){
            swalWithBootstrapButtons.fire(
                '   ویرایش شد ',
                '  ! کاربر مورد نظر  ',
                'success'
            )
            Navigate(-1)
        }else{
            throw(res)
        }
    }catch(res){
        swalWithBootstrapButtons.fire(
            '  مشکلی پیش آمده ',
            ' ! اینترنت شما قطع است',
            'question'
        )
        Navigate(-1)

    }
}

// Get And read Form 
export const UserGetSeviseForm = async (id , setData , setStateEditForm , Navigate) => {
    try {
        const res = await jpAxios.get(`/users/${id}`);
        console.log(res);
        if (res.status === 200) {
            setData({
                name: res.data.name,
                username: res.data.username,
                email: res.data.email,
                address: {
                    street: res.data.address.street,
                    city: res.data.address.city,
                    suite: res.data.address.suite,
                    zipcode: res.data.address.zipcode
                }
            })
            setStateEditForm(false);
        } else {
            throw (res)
        }

    } catch (res) {
        swalWithBootstrapButtons.fire(
            '  مشکلی پیش آمده ',
            ' ! اینترنت شما قطع است',
            'question'
        )
        Navigate(-1)

    }
}

// reload all users 
export const UsersReloadServise = async (setUsers,setMainUsers,setFetchNumber , setUsersError) => {
    try {
        const res = await jpAxios.get("/users");
        if (res.status === 200) {
            setUsers(res.data)
            setMainUsers(res.data)
            setFetchNumber(false);
            setUsersError(false);

        } else {
            throw (res)
        }
    } catch (err) {
        setFetchNumber(false);
        setUsersError(err);

    }
}

// delete 1 user
export const UserDeleteServise = async (itemid , Users , setUsers ,setFetchNumber , setUsersError ) => {
       
    swalWithBootstrapButtons.fire({
        title: '  آیا مطمعن هستید؟ ',
        text: "! امکان بازگشت اطلاعات وجود ندارد ",
        icon: 'warning',
        showCancelButton: true,
        showCloseButton: true,
        confirmButtonText: ' بله ',
        cancelButtonText: 'لغو عملیات ',
        reverseButtons: false
    }).then((result) => {
        if (result.isConfirmed) {
            jpAxios.delete(`/users/${itemid}`).then(res => {
                if (res.status === 200) {
                    let newUsers = Users.filter(u => { return u.id !== itemid })
                    setUsers(newUsers)
                    swalWithBootstrapButtons.fire(
                        ' حذف شد ',
                        '  ! کاربر مورد نظر  ',
                        'success'
                    )
                } else {
                    throw (res);
                }
            }).catch((error) => {
                swalWithBootstrapButtons.fire(
                    '  مشکلی پیش آمده ',
                    ' ! اینترنت شما قطع است',
                    'question'
                )
                setFetchNumber(true);
                setUsers([]);
                setUsersError(error);
            });

        } else if (

            /* Read more about handling dismissals below */
            result.dismiss === Swal.DismissReason.cancel
        ) {
            swalWithBootstrapButtons.fire(
                '! عملیات لغو گردید ',
                '',
                'error'
            )
        }
    })


}

// search angine
export const SearchServise = (e , setUsers ,MainUsers , Users , setUsersSearch) => {
    setUsers(MainUsers.filter((u) => {
        return (
            u.name.includes(e.target.value)
        )
    }));
    if (Users.length === 0) {
        setUsersSearch(true)
    } else {
        setUsersSearch(false)
    }
}
