import React from "react";
import { Route, Routes } from "react-router-dom";
import Galleys from "./gallery/Galleys";
import Posts from "./posts/Posts";
import Todos from "./todos/Todos";
import Uers from "./users/Uers";
import UsersAdd from "./users/CrudUser";
import Page404 from "./Page404/Page404";

function Main() {


  return (
    <>
      <div className="col-12 col-md-9 col-lg-10 mainContent px-2" style={{ overflow: 'auto' }}>

        <div className=" mio unload">

          <Routes>

            {/* <Route path="/" element={IsUser ? <Navigate replace to={"/Users"} /> : <Navigate replace to={"/posts"} />} /> */}
            <Route path="/AdminPanelV1/users" element={<Uers />} />
            <Route path="/AdminPanelV1/users/CrudUser" element={<UsersAdd />} >
              <Route path=":Id" />
            </Route>
            <Route path="/AdminPanelV1/gallery" element={<Galleys />} />
            <Route path="AdminPanelV1/posts" element={<Posts />} />
            <Route path="AdminPanelV1/Todo" element={<Todos />} />
            <Route path="*"  element={<Page404 />} />
            
          </Routes>

        </div>
      </div>
    </>
  );


}

export default Main;
